import { Component, OnInit } from '@angular/core';
import {AlertController, ModalController} from '@ionic/angular';
import {ServiceSubTypesModalComponent} from '../components/service-sub-types-modal/service-sub-types-modal.component';
import {CommunicationService} from '../communication.service';
import {SharedServiceService} from '../../../shared-service.service';
import {ApiService} from '../../../api.service';
import {ServiceTypesModalComponent} from '../components/service-types-modal/service-types-modal.component';
import {environment} from '../../../../environments/environment';

@Component({
  selector: 'app-service-sub-types',
  templateUrl: './service-sub-types.page.html',
  styleUrls: ['./service-sub-types.page.scss'],
})
export class ServiceSubTypesPage implements OnInit {

  constructor(private sharedService: SharedServiceService, private alertController: AlertController, private communicationService: CommunicationService, private adminService: ApiService, public modalController: ModalController) { }
  subServices: any = [];
  selectedServiceType: any = null;
  async presentModal(componentData) {
    const modal = await this.modalController.create({
      component: ServiceSubTypesModalComponent,
      cssClass: 'admin-modal-class',
      backdropDismiss: true,
      swipeToClose: true,
      showBackdrop: true,
      componentProps: componentData
    });
    modal.onWillDismiss().then(() => {
      this.getMainServices();
    });
    return await modal.present();
  }

  onEdit(serviceType) {
    this.selectedServiceType = serviceType;
    this.presentModal(serviceType);
  }

  async onDelete(serviceType) {
    this.communicationService.showAdminSpinner.next(true);
    const serviceTypeId = serviceType.id;
    const alert = await this.alertController.create({
      header: `Do you want to delete ${serviceType.name}?`,
      cssClass: 'alert-popup',
      buttons: [
        {
          text: 'Cancel',
          role: 'cancel',
          handler: () => {
            this.communicationService.showAdminSpinner.next(false);
          },
        },
        {
          text: 'OK',
          role: 'confirm',
          handler: () => {
            this.deleteServiceType(serviceTypeId);
          },
        },
      ]
    });

    await alert.present();
  }

  deleteServiceType(serviceTypeId) {
    this.adminService.deleteSubService(serviceTypeId).subscribe(
        res => this.deleteServiceTypeSuccess(res),
        error => {
          this.adminService.commonError(error);
        }
    );
  }

  deleteServiceTypeSuccess(res) {
    this.sharedService.presentToast('Sub Service deleted Successfully.', 'success');
    this.getMainServices();
    this.communicationService.showAdminSpinner.next(false);
  }

  ionViewWillEnter() {
    this.communicationService.pageTitle.next('Sub Services');
  }

  ionViewWillLeave() {
    this.closeModal();
  }

  async closeModal() {
    const onClosedData: string = 'Wrapped Up!';
    await this.modalController.dismiss(onClosedData);
  }

  ngOnInit() {
    this.communicationService.pageTitle.next('Sub Services');
    this.getMainServices();
  }

  getMainServices() {
    this.adminService.getAllSubService().subscribe(
        res => this.getAllSubServiceSuccess(res),
        error => {
          this.adminService.commonError(error);
        }
    );
  }

  getAllSubServiceSuccess(res) {
    this.subServices = res;
    console.log(this.subServices, 'this.subServices');
  }

}
